
import util from '@/libs/util'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'

import common from '@/libs/common.mixin'

const emptyObject = {
bid: 0,
sid: 0,
content: ''
}

export default{
mixins: [common],
components: {
Group,
XButton,
XTextarea
},
data () {
return {
info: util.copy(emptyObject)
}
},
methods: {
send () {
this.info.sid = this.sid$
this.info.bid = this.student$.bid

if(/^\s*$/.test(this.info.content)){
this.alert('提示','请先输入反馈内容')
return
}

this.$rest('advices').post(this.info)
.success(data=>{
this.toast('提交成功','success')
this.info = util.copy(emptyObject)
setTimeout(()=>{
this.$router.go(-1)
},800)
}).error(response=>{
this.alert('提示',response.body.message||'提交失败')
})

}
}
}



